
@charset "UTF-8";
* {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  outline: none;
  margin: 0;
  padding: 0;
}
html {
  -webkit-text-size-adjust: none;
  line-height: 1.42857143;
  font-size: 14px;
  color: #333;
}
td, th {
  word-wrap: break-word;
  word-break: break-all;
}
ul, li {
  list-style: none;
}
img {
  border: 0;
}
a {
  text-decoration: none;
}
abbr, acronym {
  border: 0;
  font-variant: normal;
}
del, ins {
  text-decoration: none;
}
caption, th {
  text-align: left;
}
sup, sub {
  vertical-align: baseline;
}
h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
}
q:before, q:after {
  content: '';
}
em, i {
  font-style: normal;
}
body {
  font-family: "微软雅黑","Microsoft YaHei","宋体",helvetica,"Hiragino Sans GB";
  color: #333333;
}
body {
  background-color: rgba(153, 204, 153, 0.4);
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 10rem;
  margin: 0 auto;
}
#app .mg-l {
    margin-left: 20px;
}



